import { ListItem } from "@react-native-material/core";
import axios from "axios";
import { useState } from "react";
import { useEffect } from "react";
import { ScrollView, StyleSheet, View } from "react-native";
import { constants } from "../models/contants";

const QuotesScreen = ({ navigation }) => {
  const [quotes, setQuotes] = useState([]);

  useEffect(() => {
    init();
  }, []);

  const init = async () => {
    const url =
      constants.BASE_URL + "/api/quote-action/data/hard times/heartbroken/0/data.json";
    const request = axios.create({ baseURL: url, timeout: 5000 });
    const res = await request.get(url);
    setQuotes(res.data.contents.map((c) => ({ title: c })));
  };

  const onPressHandler = (quote) => {
    navigation.navigate("QuoteDetail", { quote });
  };

  return (
    <ScrollView>
      <View style={{ backgroundColor: "white", flex: 1 }}>
        {quotes &&
          quotes.map((q, index) => (
            <ListItem
              key={index}
              title={q.title}
              onPress={() => onPressHandler(q)}
            />
          ))}
      </View>
    </ScrollView>
  );
};

const styles = StyleSheet.create({
  container: {
    width: 150,
    height: "40%",
    margin: 10,
    backgroundColor: "grey",
  },
});
export default QuotesScreen;
