import { Storage } from "aws-amplify";
import axios from "axios";
import { useDispatch } from "react-redux";

export default class HomeService {
  dispatch;
  constructor() {
    this.dispatch = useDispatch();
  }

  async getCategories() {
    const awsUrl = await Storage.get('data/quotes/category.json');
    const request = axios.create({baseURL: awsUrl, timeout: 5000});
    const res = await request.get(awsUrl);
    return res.data;
  }
}
