import {
  Button, Text
} from "@react-native-material/core";
import { useIsFocused } from "@react-navigation/native";
import BoxLabel from "components/home/box-label";
import TitleBoxs from "components/home/title-boxs";
import React, { useEffect } from "react";
import { ScrollView, StyleSheet, View } from "react-native";
import { useSelector } from "react-redux";
import HomeTaskList from "../components/home/task-list";
import { log } from "../components/utils/log";
import { constants, layoutConstants } from "../models/contants";
import HomeService from "../services/home-service";
import { lineStyle } from "../styles/component-style";
import TaskService from "../services/task-service";

const HomeScreen = ({ navigation }) => {
  const homeService = new HomeService();
  const taskService = new TaskService();
  const [categories, setCategories] = React.useState(null);
  const tasks = useSelector((state) =>
    state.task.value.filter((c) => !c.deleted)
  );
  const focus = useIsFocused();
  useEffect(() => {
    const getCategoriesSync = async () => {
      const cate = await homeService.getCategories();
      setCategories(cate);
    };
    getCategoriesSync();
  }, []);

  useEffect(() => {
    focus && taskService.getTasksByCategoryStore(constants.RECENT_ACTIONS);
  }, [focus]);

  const onPressTaskList = () => {
    navigation.navigate("Actions");
  };

  const onQuotePress = (tag) => {
    log.debug(tag);
    navigation.navigate("QuoteDetail", { url: tag.url });
  };

  return (
    <View style={styles.container}>
      <ScrollView>
        <BoxLabel
          style={styles.box}
          label={
            'They may call your victories, "lucky"...They may mock your failures...They may laugh at your heartache...But still, they will never be happy. This is the cyclical emptiness of the hater mindset.'
          }
          onPress={() => {}}
          textStyle={{ color: layoutConstants.colorPrimary }}
        ></BoxLabel>
        <View style={{ margin: 10 }}>
          {tasks ? (
            <View>
              <Text variant="h5">Recent Actions</Text>
              <HomeTaskList
                tasks={tasks.reverse()}
                onPress={onPressTaskList}
              ></HomeTaskList>
              <Button
                title="Click to see more actions"
                variant="text"
                onPress={onPressTaskList}
                color={layoutConstants.colorPrimary}
              />
            </View>
          ) : (
            <Button
              variant="text"
              onPress={onPressTaskList}
              color={layoutConstants.colorPrimary}
            >
              Click to create first action
            </Button>
          )}
        </View>

        {categories &&
          Object.keys(categories).map((k, index) => (
            <View key={index}>
              <View
                style={{
                  ...lineStyle.line,
                  borderBottomColor: "#339F9F",
                  marginHorizontal: 10,
                }}
              />
              <TitleBoxs key={k} navigation={navigation} title={k}>
                {categories[k] &&
                  categories[k].map((c, index) => {
                    c.tag = c.tag.charAt(0).toUpperCase() + c.tag.slice(1);
                    return (
                      <BoxLabel
                        key={index}
                        navigation={navigation}
                        label={c.tag}
                        onPress={() => onQuotePress(c)}
                      ></BoxLabel>
                    );
                  })}
              </TitleBoxs>
            </View>
          ))}
      </ScrollView>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: layoutConstants.bgColorPrimary,
    flex: 1,
  },
  box: {
    height: 100,
    borderRadius: 10,
    marginHorizontal: 30,
    marginTop: 20,
    padding: 5,
    color: "#489C93",
    borderWidth: 2,
    borderColor: "#40A79E",
    backgroundColor: "#E5FFFD",
  },
});
export default HomeScreen;
