import { Storage } from "aws-amplify";
import axios from "axios";
import { log } from "utils/log";


export default class QuoteService {
  async getList(url) {
    log.debug("getList", url);
    const awsUrl = await Storage.get(url);
    const request = axios.create({baseURL: awsUrl, timeout: 5000});
    const res = await request.get(awsUrl);
    return res.data;
  }
}

let service;
const createQuoteService = () => {
  if (service) {
    return service;
  }
  service = new QuoteService();
  return service;
};

export { createQuoteService };