import { Text } from "@react-native-material/core";
import * as React from "react";
import { Pressable, StyleSheet, View } from "react-native";

export default function BoxLabel({ style, label, textStyle, onPress }) {
  return (
    <Pressable style={style ? style : styles.box} onPress={onPress}>
      <View style={{ alignItems: "center",  justifyContent: "center", flex: 1 }}>
        <Text style={textStyle ? textStyle : ""}>{label || "Title"}</Text>
      </View>
    </Pressable>
  );
}
const styles = StyleSheet.create({
  box: {
    width: 150,
    height: "45%",
    borderRadius: 10,
    margin: 5,
    padding: 5,
    backgroundColor: "#E7EBEB",
  },
});
