import { Divider, Text } from "@react-native-material/core";
import * as React from "react";
import { ScrollView, View } from "react-native";

export default function TitleBoxs({
  title,
  children,
}) {
  const upperTitle = title.charAt(0).toUpperCase() + title.slice(1);
  return (
    <View style={{ margin: 10 }}>
      <Text variant="h5">{upperTitle || "Title"}</Text>
      <ScrollView horizontal style={{ flexGrow: 0, marginVertical: 5 }}>
        <View
          style={{
            display: "flex",
            flexWrap: "wrap",
            flexDirection: "column",
            height: 200,
          }}
        >
          {children}
        </View>
      </ScrollView>
    </View>
  );
}
